import React from 'react';
import  bootstrap from "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import {NavLink} from 'react-router-dom';
import index from './index.css';




const Navbar=()=>{

		return(
				
<>
		

				<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <NavLink exact className="navbar-brand brand" to="/">ImageBazzar</NavLink>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">

      <li className="nav-item active">

        <NavLink exact activeClassName="menu" className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
      </li>
      <li className="nav-item">
        <NavLink activeClassName="menu" className="nav-link" to="/contact">Contact</NavLink>
      </li>

      <li className="nav-item">
        <NavLink activeClassName="menu" className="nav-link" to="/about">About</NavLink>
      </li>
     
      
    </ul>
    
  </div>
</nav>

			</>		

				

		)
};

export default Navbar;